/* Toggle Styles */

._38Jgy {
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
    display: -webkit-box;
    display: flex;
    -webkit-box-flex: 1;
            flex: 1;
}

/* Sidebar Styles */

._2hIZM {
    position: relative;
    float: left;
    width: 100%;
    height: 100%;
    overflow-y: auto;
    background: #43425d;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
}

a,
li {
    cursor: pointer;
}

._2hIZM header {
    background-color: #8684ba;
    font-size: 20px;
    line-height: 70px;
    text-align: center;
    font-family: "B612", sans-serif;
}

._2hIZM header a {
    color: #fff;
    display: block;
    text-decoration: none;
    font-family: "B612", sans-serif;
}

._2hIZM header a:hover {
    color: #fff;
}

._3PxtN > li > a,
._3z5qk > li > a {
    position: relative;
    display: block;
    padding: 10px 15px;
    text-decoration: none;
}

._3PxtN,
._3z5qk {
    padding: 0;
    list-style: none;
    margin: 0;
}

._2hIZM ._3PxtN a,
._3z5qk a {
    border-bottom: 1px solid #455a64;
    color: #cfd8dc;
    font-size: 14px;
    padding: 16px 24px;
    font-family: "B612", sans-serif;
}

._3z5qk {
    overflow-y: scroll;
    height: 400px;
}

._2hIZM ._3PxtN a:hover,
._3z5qk a:hover {
    color: #eceff1;
    background-color: #35303e;
    border-left: 5px solid #8684ba;
    -webkit-transition: all 0.5s ease 0s;
    transition: all 0.5s ease 0s;
}

._2uVFC {
    color: #eceff1;
    background-color: #35303e;
    border-left: 5px solid #8684ba;
    -webkit-transition: all 0.5s ease 0s;
    transition: all 0.5s ease 0s;
}

._2hIZM ._3PxtN a i {
    margin-right: 16px;
}

._3HSOi {
    display: block;
    position: relative;
}

._2ck2L {
    display: block;
    position: relative;
}
